// Color
$c-black:         #000;
$c-white:         #fff;

$c-grey-lighter:  #F1F1F2;
$c-grey-light:    #DCDDDE;
$c-grey:          #767778;

$c-red:           #ff0033;
$c-red-light:     #FFADC1;
$c-green:         #2A8661;
$c-green-light:   #42AC82;

$c-primary:         $c-red;
$c-secondary:       $c-red-light;
$c-confirm:         $c-green-light;
$c-confirm-dark:    $c-green;

// Font
$font-family:       'Fira Sans', sans-serif;

$fs-small:          1rem;       //paragraph
$fs-medium:         1.125rem;   //buttons, h3, h4
$fs-large:          1.375rem;   //input
$fs-xlarge:         1.5rem;     //h1, h2
$fs-xxlarge:        1.625rem;

$lh-text:           1.25;
$lh-head3:          1.44;
$lh-head2:          1.167;
$lh-head1:          1.417;

// Layout
$gutter-xsmall:      8px;
$gutter-small:       16px;
$gutter-medium:      24px;
$gutter-large:       32px;
$gutter-xlarge:      40px;
$gutter-xxlarge:     56px;
