@import '../../styles/Base.scss';

.textfield {
    input {
        @include font(regular);
        font-size: $fs-large;
        line-height: 25px;
        color: $c-primary;
        caret-color: $c-confirm;
        width: 100%;
        margin: 0 0 $gutter-medium;
        padding: 10px 0;
        background-color: $c-white;
        border: 0;
        text-align: center;

        &::placeholder {
            color: $c-primary;
            opacity: 1;
        }

        &:focus {
            outline: none;
        }
    }

    &NotSticky {
        input {
        }
    }
}
