@import '../../styles/Base.scss';

.primary {
    @include button(primary);
}

.secondary {
    @include button(secondary);
}

.back {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 45px;
  width: 45px;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;

  svg {
    display: block;
    height: 17px;
    width: 10px;
    margin-left: -2px;
    transform: rotate(180deg);
    fill: $c-red;
  }

  &:focus {
      outline: none;
  }
}
