@import '../../styles/Base';

.input {
    display: none;
}

.button {
    @include blockWidth();
    text-align: center;

    .label {
        @include button(secondary);
    }
}

.list {
    @include blockWidth();
    padding: 0 $gutter-large;

    .label {
        @include font(regular);
        font-size: $fs-medium;
        line-height: $lh-text;
        color: $c-primary;
        text-align: center;
        box-sizing: border-box;
        display: block;
        margin: 0;
        padding: 11px 0;
        width: 100%;
        transition: font-weight .2s ease-out;
        border-bottom: 1px solid $c-grey-light;

        &:hover,
        &:focus,
        &:active {
            @include font(bold);
            cursor: pointer;
        }
    }

    + *:not(.list) {
        margin-top: $gutter-medium;
    }
}

.slider {
    display: inline-block;

    .label {
        @include font(regular);
        font-size: $fs-xxlarge;
        line-height: 100px;
        color: $c-primary;
        background-color: $c-white;
        text-align: center;
        box-sizing: border-box;
        display: inline-block;
        margin: 0;
        padding: 0;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 1px solid $c-grey-light;
        transition: background-color .2s ease-out, font-weight .2s ease-out;


        &:active {
            @include font(bold);
            color: $c-white;
            background-color: $c-primary;
        }
    }
}
