@import '../../styles/Base.scss';

.title {
    @include blockWidth();
    @include font(bold);
    font-size: $fs-xlarge;
    line-height: $lh-head1;
    color: $c-black;
    text-align: center;
    margin: $gutter-large 0 $gutter-xsmall;
    padding: 0 $gutter-large;

    + * {
        // !important used to override any margin-top
        margin-top: 0 !important;
    }
}
