@mixin button($variation: primary) {
    @include font(regular);
    font-size: $fs-medium;
    line-height: 25px;
    color: $c-primary;
    text-align: center;
    vertical-align:top;
    box-sizing: border-box;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0 auto $gutter-small;
    padding: 10px 40px;
    min-width: 200px;
    max-width: 280px;
    height: 45px;
    border-radius: 23px;
    border: none;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0);
    transition:
        background-color .2s ease-out,
        color .2s ease-out,
        border .2s ease-out,
        box-shadow .2s ease-out;
    position: relative;
    user-select: none;
    cursor: pointer;

    svg {
        width: 10px;
        height: 17px;
        display: block;
        position: absolute;
        right: 15px;
        top: 14px;
    }

    @if $variation == secondary {
        background: $c-white;
        padding: 9px 40px;
        border: 1px solid $c-primary;

        svg {
            fill: $c-primary;
        }
    }

    @else if $variation == primary {
        background: $c-primary;
        color: $c-white;

        svg {
            fill: $c-white;
        }
    }

    &:hover,
    &:focus,
    &:active {
        @include font(bold);
        outline: none;
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.15);
        color: $c-white;

        svg {
            fill: $c-white;
        }
    }

    &:hover,
    &:focus {
        background: $c-confirm;
        border-color: $c-confirm;
    }

    &:active {
        background: $c-confirm-dark;
        border-color: $c-confirm-dark;
    }
}
