@import '../../styles/Base.scss';

.Progress{
    width: 100%;
    background-color: $c-secondary;
    height: 2px;
    margin: 0 0 $gutter-medium;
    position: relative;

    &Current {
        background-color: $c-primary;
        width: 0;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
    }

    &:first-child {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
    }


    &:last-child {
        position: fixed;
        margin: 0;
        bottom: 0;
        right: 0;
        left: 0;
    }
}
