@import './styles/Base.scss';

body {
  @include font(regular);
  font-size: $fs-small;
  line-height: $lh-text;
  color: $c-black;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.App {
  margin: 0 auto;
  width: 100%;
  background-color: $c-white;
}
