@import '../../styles/Base.scss';

.message {
    @include blockWidth();
    @include font(regular);
    text-align: center;
    font-size: $fs-xlarge;
    line-height: $lh-head2;
    color: $c-black;
    position: relative;
    padding: $gutter-medium $gutter-large;
    margin: 0 0 $gutter-medium;

    &::before,
    &::after {
        @include font(regular);
        content: '“';
        position: absolute;
        font-size: 50px;
        line-height: 1;
        color: $c-primary;
        opacity: 0.3;
    }
    &::before {top: 0; left: $gutter-large;}
    &::after {bottom: -30px; right: $gutter-large;}

    p {
        @include font(regular);
        text-align: center;
        font-size: $fs-xlarge;
        line-height: $lh-head2;
        color: $c-black;
        margin: 0 0 $gutter-small;
        padding: 0;
    }

    a {
        color: $c-primary;
        text-decoration: underline;

        @media (hover: hover) {
            &:hover,
            &:focus {
                cursor: pointer;
            }
        }
    }

    ul, ol {
        padding: 0 0 0 $gutter-small;
        margin: 0 0 $gutter-small;

        li {
            @include font(regular);
            font-size: $fs-small;
            line-height: $lh-text;
            color: $c-black;
            margin: 0 0 5px;
            padding: 0;
        }
    }

    strong {
        @include font(bold);
    }

    em {
        font-style: italic;
    }

    > *:last-child {
        margin-bottom: 0;
    }
}

.zipcode {
    @include blockWidth();
    @include font(regular);
    text-align: center;
    font-size: $fs-large;
    line-height: $lh-text;
    color: $c-primary;
    padding: 0;
    margin: 0;
}
