@import '../../styles/normalizer.scss';
@import '../../styles/Base.scss';

$breakpoint-middel: 720px;

.result {
    @include blockWidth(720px);
    margin: 0 auto;
    text-align: left;

    @media (min-width: $breakpoint-middel) {
        padding: 0 24px;
    }

    h1 {
        @include font(bold);
        font-size: $fs-xlarge;
        line-height: $lh-head1;
        color: $c-black;
        margin: 0 0 $gutter-small;
        padding: 0;

        @media (min-width: $breakpoint-middel) {
            font-size: $fs-xxlarge;
        }
    }

    h2 {
        @include font(regular);
        font-size: $fs-xlarge;
        line-height: $lh-head2;
        color: $c-black;
        margin: 0 0 $gutter-small;
        padding: 0;

        @media (min-width: $breakpoint-middel) {
            font-size: $fs-xxlarge;
        }
    }

    h3 {
        @include font(bold);
        font-size: $fs-medium;
        line-height: $lh-head3;
        color: $c-black;
        margin: 0 0 $gutter-xsmall;
        padding: 0;

        @media (min-width: $breakpoint-middel) {
            font-size: $fs-large;
        }
    }

    h4 {
        @include font(regular);
        font-size: $fs-medium;
        line-height: $lh-head3;
        color: $c-black;
        margin: 0;
        padding: 0;
    }

    p {
        @include font(regular);
        font-size: $fs-small;
        line-height: $lh-text;
        color: $c-black;
        margin: 0 0 $gutter-medium;
        padding: 0;

        @media (min-width: $breakpoint-middel) {
            font-size: $fs-medium;
        }
    }

    a {
        color: $c-primary;
        text-decoration: underline;

        @media (hover: hover) {
            &:hover,
            &:focus {
                cursor: pointer;
            }
        }
    }

    ul, ol {
        padding: 0;
        margin: 0 0 $gutter-small;

        li {
            @include font(regular);
            font-size: $fs-small;
            line-height: $lh-text;
            color: $c-black;
            margin: 0 0 $gutter-small;
            padding: 0;
            list-style-type: none;
            list-style-position: inside;

            @media (min-width: $breakpoint-middel) {
                font-size: $fs-medium;
            }

            &::before {
                content: '\2713';
                display: inline-block;
                margin-right: 4px;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &.specilList {
            padding-left: $gutter-large;

            li {
                margin-bottom: $gutter-medium;
                list-style-position: outside;
                position: relative;

                &::before {
                    content: '';
                    height: 24px;
                    width: 24px;
                    border-radius: 12px;
                    background-color: $c-confirm;
                    background-image: url(../../images/icon-check.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: contain;
                    position: absolute;
                    left: -32px;
                    top: -3px;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &.productList {
            padding-left: 0;
            display: flex;
            flex-wrap: wrap;

            li {
                box-sizing: border-box;
                margin-bottom: 0;
                padding: $gutter-medium $gutter-small $gutter-small;
                list-style-position: outside;
                list-style: none;
                text-align: center;
                flex: 0 0 50%;
                border-bottom: 1px solid $c-grey-light;

                @media (min-width: $breakpoint-middel) {
                    flex: 0 0 33.33%;
                }

                .productImage {
                    position: relative;
                    padding-top: 100%;
                    width: 100%;

                    img {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        width: 100%;
                    }
                }

                &::before {
                    content: '';
                    display: none;
                }
            }
        }
    }

    table {
        width: 100%;
        margin: 0 0 $gutter-medium;
        border-collapse: collapse;

        tr {
            border-bottom: 1px solid $c-grey-light;

            td {
                @include font(regular);
                font-size: $fs-small;
                line-height: $lh-text;
                color: $c-black;
                padding: $gutter-small 0;

                @media (min-width: $breakpoint-middel) {
                    font-size: $fs-medium;
                }

                &:last-child {
                    text-align: right;
                }
            }
        }
    }

    strong {
        @include font(bold);
    }

    em {
        font-style: italic;
    }

    > *:last-child {
        margin-bottom: 0;
    }

    .row {
        box-sizing: border-box;
        padding: $gutter-medium $gutter-small $gutter-xxlarge;

        > *:last-child {
            margin-bottom: 0;
        }
    }

    .hero {
        padding: $gutter-medium $gutter-small;
        text-align: center;

        img {
            width: 100%;
            max-width: 400px;
        }
    }

    .usp {
        padding: $gutter-small $gutter-small $gutter-xxlarge;
        border-top: 1px solid $c-grey-light;
        text-align: center;

        &Breakin,
        &Fire,
        &Assault {
            color: $c-red;

            &::before {
                content: '';
                display: block;
                height: 56px;
                width: 56px;
                margin-bottom: $gutter-xsmall;
                background-image: url(../../images/result-icon-breakin.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }

        &Fire {
            &::before {
                background-image: url(../../images/result-icon-fire.svg);
            }
        }

        &Assault {
            &::before {
                background-image: url(../../images/result-icon-assault.svg);
            }
        }
    }

    .tp {
        padding: $gutter-small $gutter-small $gutter-xxlarge;
        text-align: center;

        iframe {
            position: relative;
            height: 130px;
            width: 100%;
            border-style: none;
            display: block;
            overflow: hidden;
        }
    }

    .rowGrey {
        position: relative;
        z-index: 1;

        &:before {
            background-color: $c-grey-lighter;
            content: '';
            height: 100%;
            position: absolute;
            width: 100vW;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            z-index: -1;
            display: block;
        }
    }

    .rowGreyDark {
        position: relative;
        color: $c-white;
        z-index: 1;

        h1, h2, h3, h4, p, li, td {
            color: $c-white;
        }

        &:before {
            background-color: $c-grey;
            content: '';
            height: 100%;
            position: absolute;
            width: 100vW;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            z-index: -1;
            display: block;
        }
    }

    .col3 {
        display: flex;
        justify-content: space-evenly;
        padding-top: $gutter-small;
    }

    .red {
        color: $c-red;
    }
}
