@import '../../styles/Base.scss';

.paragraph,
.quote,
.checkboxed {
    @include blockWidth();
    padding: 0 $gutter-small;
    margin: 0 0 $gutter-medium;
    text-align: left;

    h1 {
        @include font(bold);
        font-size: $fs-xlarge;
        line-height: $lh-head1;
        color: $c-black;
        margin: 0 0 $gutter-medium;
        padding: 0;
    }

    h2 {
        @include font(regular);
        font-size: $fs-xlarge;
        line-height: $lh-head2;
        color: $c-black;
        margin: 0 0 $gutter-medium;
        padding: 0;
    }

    h3 {
        @include font(bold);
        font-size: $fs-medium;
        line-height: $lh-head3;
        color: $c-black;
        margin: 0 0 $gutter-small;
        padding: 0;
    }

    p {
        @include font(regular);
        font-size: $fs-small;
        line-height: $lh-text;
        color: $c-black;
        margin: 0 0 $gutter-small;
        padding: 0;
    }

    a {
        color: $c-primary;
        text-decoration: underline;

        @media (hover: hover) {
            &:hover,
            &:focus {
                cursor: pointer;
            }
        }
    }

    ul, ol {
        padding: 0 0 0 $gutter-small;
        margin: 0 0 $gutter-small;

        li {
            @include font(regular);
            font-size: $fs-small;
            line-height: $lh-text;
            color: $c-black;
            margin: 0 0 5px;
            padding: 0;
        }
    }

    strong {
        @include font(bold);
    }

    em {
        font-style: italic;
    }

    > *:last-child {
        margin-bottom: 0;
    }
}

.quote {
    @include font(regular);
    font-size: $fs-xlarge;
    line-height: $lh-head2;
    color: $c-black;
    text-align: center;
    position: relative;
    padding: $gutter-medium $gutter-large;
    margin: 0 0 $gutter-medium;

    &::before,
    &::after {
        @include font(regular);
        content: '“';
        position: absolute;
        font-size: 50px;
        line-height: 1;
        color: $c-primary;
        opacity: 0.3;
    }
    &::before {top: 0; left: $gutter-large;}
    &::after {bottom: -30px; right: $gutter-large;}
}

.checkboxed {
    padding: 0 $gutter-large;

    p {
        @include font(regular);
        font-style: italic;
        font-size: $fs-small;
        line-height: $lh-text;
        color: $c-black;
        position: relative;
        border-radius: 4px;
        border: 1px solid $c-grey-light;
        padding: $gutter-small $gutter-small $gutter-small $gutter-xlarge;
        margin: 0 0 $gutter-medium;

        &::before {
            @include font(regular);
            content: '';
            position: absolute;
            height: 24px;
            width: 24px;
            border-radius: 12px;
            background-color: $c-confirm;
            background-image: url(../../images/icon-check.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            top: 50%;
            left: $gutter-xsmall;
            transform: translateY(-50%);
        }
    }
}
